import {Component, OnInit} from '@angular/core';
import {animate, style, transition, trigger} from '@angular/animations';
import { GetAllUserService } from '../../services/GetAllUserService';

@Component({
  selector: 'app-list-user',
  templateUrl: './list-user.component.html',
  styleUrls: [
    './list-user.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [GetAllUserService],
    animations: [
    trigger('fadeInOutTranslate', [
      transition(':enter', [
        style({opacity: 0}),
        animate('400ms ease-in-out', style({opacity: 1}))
      ]),
      transition(':leave', [
        style({transform: 'translate(0)'}),
        animate('400ms ease-in-out', style({opacity: 0}))
      ])
    ])
  ]
})
export class UserListComponent implements OnInit {
  
  rowsFilter = [];
  tempFilter = [];
  
  constructor(public _getAllUserService: GetAllUserService) {
  }

  ngOnInit() {
  }


  

  //Managing User List
  getUsers(id:any){
    this._getAllUserService.getAllUsers(id).subscribe(response => {
      this.rowsFilter = response.data;
      console.log(response.data);
    });
  }

  isUserBlocked(id){
    console.log(id);
    // this.rowsFilter.splice(this.rowsFilter.indexOf({ Id: id }));

    this._getAllUserService.userBlockUnblock(id).subscribe(response=> {
      var element = this.rowsFilter.find(x => x.Id == id);

      if(element.Id == id){
        this.rowsFilter.splice(element, 1);
        this.rowsFilter.push(response.data)
      }
      console.log(this.rowsFilter);
    })
  }


}



